import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link, useParams, useNavigate } from 'react-router-dom'
import { Form, Button, Dropdown } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
import FormContainer from '../components/FormContainer'
import { listAudioDetails, updateAudio } from '../actions/audioActions'
import { AUDIO_UPDATE_RESET } from '../constants/audioConstants'

const AudioEditScreen = () => {
  const params = useParams()
  const audioId = params.id
  const navigate = useNavigate()

  const [audioTitle, setAudioTitle] = useState('')
  const [mp3file, setMp3file] = useState('')
  const [category, setCategory] = useState('')
  const [subcategory, setSubcategory] = useState('')

  const [uploading, setUploading] = useState(false)

  /* All Audios Dropdown content*/
  const audioList = useSelector((state) => state.audioList)
  const { audios } = audioList

  const dispatch = useDispatch()

  const audioDetails = useSelector((state) => state.audioDetails)
  const { loading, error, audio } = audioDetails

  const audioUpdate = useSelector((state) => state.audioUpdate)
  const {
    loading: loadingUpdate,
    error: errorUpdate,
    success: successUpdate,
  } = audioUpdate

  useEffect(() => {
    if (successUpdate) {
      dispatch({ type: AUDIO_UPDATE_RESET })
      navigate('/admin/audio')
    } else {
      if (!audio.audioTitle || audio._id !== audioId) {
        dispatch(listAudioDetails(audioId))
      } else {
        setAudioTitle(audio.audioTitle)
        setMp3file(audio.mp3file)
        setCategory(audio.category)
        setSubcategory(audio.subcategory)
      }
    }
  }, [dispatch, navigate, audioId, audio, successUpdate, audios])

  const uploadFileHandler = async (e) => {
    const file = e.target.files[0]
    const formData = new FormData()
    formData.append('upload', file)
    setUploading(true)

    try {
      const config = {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }

      const { data } = await axios.post('/api/upload', formData, config)

      setMp3file(data)
      setUploading(false)
    } catch (error) {
      console.error(error)
      setUploading(false)
    }
  }

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(
      updateAudio({
        _id: audioId,
        audioTitle,
        mp3file,
        category,
        subcategory,
      })
    )
  }

  return (
    <>
      <Link to='/admin/audio' className='btn btn-back my-3'>
        Nasp
      </Link>
      <FormContainer>
        <h1>Audio</h1>
        {loadingUpdate && <Loader />}
        {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
        {loading ? (
          <Loader />
        ) : error ? (
          <Message variant='danger'>{error}</Message>
        ) : (
          <Form onSubmit={submitHandler}>
            <Form.Group controlId='audio-title'>
              <Form.Label>
                Nzov (napr. Boh v liste Rimanom I., tak sa to zobraz v mp3
                prehrvai )
              </Form.Label>
              <Form.Control
                type='text'
                placeholder='Nzov'
                value={audioTitle}
                onChange={(e) => setAudioTitle(e.target.value)}
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId='audio-file' className='mb-3'>
              <Form.Label>Mp3 sbor</Form.Label>
              <Form.Control
                type='text'
                placeholder='Mp3 sbor'
                value={mp3file}
                readOnly
                // onChange={(e) => setMp3file(e.target.value)}
              ></Form.Control>
              <Form.Control
                type='file'
                onChange={uploadFileHandler}
              ></Form.Control>
              {uploading && <Loader />}
            </Form.Group>

            <Form.Group controlId='category'>
              <Form.Label>Kategria</Form.Label>
              <Dropdown>
                <Dropdown.Toggle
                  variant='success'
                  id='dropdown-basic'
                  className='category-dropdown'
                >
                  Kategria
                </Dropdown.Toggle>
                <Dropdown.Menu>
                  <Dropdown.Item
                    key='Slov ivota'
                    value='Slov ivota'
                    onClick={() => setCategory('Slov ivota')}
                  >
                    <h5 className='language-dropdown-lang'>Slov ivota</h5>
                  </Dropdown.Item>
                  <Dropdown.Item
                    key='tdium ivota'
                    value='tdium ivota'
                    onClick={() => setCategory('tdium ivota')}
                  >
                    <h5 className='language-dropdown-lang'>tdium ivota</h5>
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>

              <Form.Control
                type='text'
                placeholder='Kategria'
                value={category}
                onChange={(e) => setCategory(e.target.value)}
              ></Form.Control>
            </Form.Group>

            {category !== 'tdium ivota' && (
              <Form.Group controlId='subcategory' className='my-3'>
                <Form.Label>Podkategria</Form.Label>
                <Dropdown>
                  <Dropdown.Toggle
                    variant='success'
                    id='dropdown-basic'
                    className='category-dropdown'
                  >
                    Podkategria
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    <Dropdown.Item
                      key='Boh v liste Rimanom'
                      value='Boh v liste Rimanom'
                      onClick={() => setSubcategory('Boh v liste Rimanom')}
                    >
                      <h5 className='language-dropdown-lang'>
                        Boh v liste Rimanom
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Bo evangelium'
                      value='Bo evangelium'
                      onClick={() => setSubcategory('Bo evangelium')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Bo evangelium
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Charakter Pnovho pracovnka'
                      value='Charakter Pnovho pracovnka'
                      onClick={() =>
                        setSubcategory('Charakter Pnovho pracovnka')
                      }
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Charakter Pnovho pracovnka
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='lovek a dva stromy'
                      value='lovek a dva stromy'
                      onClick={() => setSubcategory('lovek a dva stromy')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        lovek a dva stromy
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Hlavn Kristove kroky'
                      value='Hlavn Kristove kroky'
                      onClick={() => setSubcategory('Hlavn Kristove kroky')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Hlavn Kristove kroky
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Fakt vra a proitek'
                      value='Fakt vra a proitek'
                      onClick={() => setSubcategory('Fakt vra a proitek')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Fakt vra a proitek
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='KESANSK IVOT'
                      value='KESANSK IVOT'
                      onClick={() => setSubcategory('KESANSK IVOT')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        KESANSK IVOT
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Kristovo vzkriesenie'
                      value='Kristovo vzkriesenie'
                      onClick={() => setSubcategory('Kristovo vzkriesenie')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Kristovo vzkriesenie
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Kristus ako zutovnica'
                      value='Kristus ako zutovnica'
                      onClick={() => setSubcategory('Kristus ako zutovnica')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Kristus ako zutovnica{' '}
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Naplnenie starho zkona'
                      value='Naplnenie starho zkona'
                      onClick={() => setSubcategory('Naplnenie starho zkona')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Naplnenie starho zkona{' '}
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Nevystihnuten Kristovo bohatstvo'
                      value='Nevystihnuten Kristovo bohatstvo'
                      onClick={() =>
                        setSubcategory('Nevystihnuten Kristovo bohatstvo')
                      }
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Nevystihnuten Kristovo bohatstvo
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='O loveku'
                      value='O loveku'
                      onClick={() => setSubcategory('O loveku')}
                    >
                      <h5 className='language-dropdown-lang'> O loveku</h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='O Duchu'
                      value='O Duchu'
                      onClick={() => setSubcategory('O Duchu')}
                    >
                      <h5 className='language-dropdown-lang'> O Duchu</h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='O Kristovi'
                      value='O Kristovi'
                      onClick={() => setSubcategory('O Kristovi')}
                    >
                      <h5 className='language-dropdown-lang'> O Kristovi</h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Porta sa s hriechmi'
                      value='Porta sa s hriechmi'
                      onClick={() => setSubcategory('Porta sa s hriechmi')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Porta sa s hriechmi
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Porta sa so svetom'
                      value='Porta sa so svetom'
                      onClick={() => setSubcategory('Porta sa so svetom')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Porta sa so svetom
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Poselstv evangelia'
                      value='Poselstv evangelia'
                      onClick={() => setSubcategory('Poselstv evangelia')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Poselstv evangelia
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Provn Krista'
                      value='Provn Krista'
                      onClick={() => setSubcategory('Provn Krista')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Provn Krista
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='ADA PRO NOV VC'
                      value='ADA PRO NOV VC'
                      onClick={() => setSubcategory('ADA PRO NOV VC')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        ADA PRO NOV VC
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Rok milosti'
                      value='Rok milosti'
                      onClick={() => setSubcategory('Rok milosti')}
                    >
                      <h5 className='language-dropdown-lang'> Rok milosti</h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Sksenosti veriacich s Kristovm vzkriesenm'
                      value='Sksenosti veriacich s Kristovm vzkriesenm'
                      onClick={() =>
                        setSubcategory(
                          'Sksenosti veriacich s Kristovm vzkriesenm'
                        )
                      }
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Sksenosti veriacich s Kristovm vzkriesenm
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Sksenos ivota'
                      value='Sksenos ivota'
                      onClick={() => setSubcategory('Sksenos ivota')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Sksenos ivota
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Spasenie'
                      value='Spasenie'
                      onClick={() => setSubcategory('Spasenie')}
                    >
                      <h5 className='language-dropdown-lang'> Spasenie</h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='truktra Boieho evanjelia'
                      value='truktra Boieho evanjelia'
                      onClick={() =>
                        setSubcategory('truktra Boieho evanjelia')
                      }
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        truktra Boieho evanjelia
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Svedomie'
                      value='Svedomie'
                      onClick={() => setSubcategory('Svedomie')}
                    >
                      <h5 className='language-dropdown-lang'> Svedomie</h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Trojnsobn semeno'
                      value='Trojnsobn semeno'
                      onClick={() => setSubcategory('Trojnsobn semeno')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Trojnsobn semeno
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Uenie apotolov'
                      value='Uenie apotolov'
                      onClick={() => setSubcategory('Uenie apotolov')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Uenie apotolov
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Udeovanie ivota'
                      value='Udeovanie ivota'
                      onClick={() => setSubcategory('Udeovanie ivota')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Udeovanie ivota
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='VN BO PLN'
                      value='VN BO PLN'
                      onClick={() => setSubcategory('VN BO PLN')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        VN BO PLN
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Vzoprie sa satanovi'
                      value='Vzoprie sa satanovi'
                      onClick={() => setSubcategory('Vzoprie sa satanovi')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Vzoprie sa satanovi
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='Zjeven ivota'
                      value='Zjeven ivota'
                      onClick={() => setSubcategory('Zjeven ivota')}
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        Zjeven ivota
                      </h5>
                    </Dropdown.Item>
                    <Dropdown.Item
                      key='ZKLADN PRVKY KESANSKHO IVOTA'
                      value='ZKLADN PRVKY KESANSKHO IVOTA'
                      onClick={() =>
                        setSubcategory('ZKLADN PRVKY KESANSKHO IVOTA')
                      }
                    >
                      <h5 className='language-dropdown-lang'>
                        {' '}
                        ZKLADN PRVKY KESANSKHO IVOTA
                      </h5>
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
                <Form.Control
                  type='text'
                  placeholder='Podkategria'
                  value={subcategory}
                  onChange={(e) => setSubcategory(e.target.value)}
                ></Form.Control>
              </Form.Group>
            )}

            <Button className='my-5 btn-blue' type='submit' variant='primary'>
              Uloi
            </Button>
          </Form>
        )}
      </FormContainer>
    </>
  )
}

export default AudioEditScreen
