import { Helmet } from 'react-helmet'
import React from 'react'

const Meta = ({ title, description, keywords }) => {
  return (
    <Helmet>
      <title>{title}</title>
      <meta name='description' content={description} />
      <meta name='keyword' content={keywords} />
    </Helmet>
  )
}

Meta.defaultProps = {
  title: 'Prd',
  description: 'Prina bohatstvo Boieho slova celmu Boiemu udu',
  keywords:
    'kresansk knihy, kresansk literatra, duchovn knihy, duchovn literatra, Boh, Trojjedin Boh, Kristus, Jei Kristus, Duch, Duch Svt, ivot, tdium ivota, Biblia, Svt Biblia, tdium Biblie, Psmo, Svt Psmo, kresanstvo, kresania, cirkev, Cirkev, miestna cirkev, miestne cirkvy',
}

export default Meta
