import React from 'react'
import { Button } from 'react-bootstrap'
import { Link } from 'react-router-dom'

const Give2Physical = () => {
  const file1 = '2022_priloha_c_1_ziadost_o_rocne_zuctovanie.pdf'
  const file2 = '2022_priloha_c_2_potvrdenie_o_zaplateni_dane.pdf'
  const file3 = '2022_priloha_c_3_vyhlasenie_o_poukazani_dane.pdf'
  const file4 = '2022_priloha_c_3a_poucenie_k_vyhlaseniu_o_poukazani_dane.pdf'
  const downloadFileHandler = (fileName) => {
    console.log(fileName)
    fetch(`${fileName}`).then((response) => {
      response.blob().then((blob) => {
        // Creating new object of PDF file
        const fileURL = window.URL.createObjectURL(blob)
        // Setting various property values
        let alink = document.createElement('a')
        alink.href = fileURL
        alink.download = `${fileName}`
        alink.click()
      })
    })
  }

  return (
    <>
      <Link className='btn btn-back my-3' to='/'>
        Nasp
      </Link>
      <div className='give2-physical'>
        <h1 className='my-5'>FYZICK OSOBY</h1>
        <strong>Postup pre fyzick osoby  zamestnancov</strong>
        <p>
          (Postup pre fyzick osoby, ktor si samy podvaj daov priznanie
          <a href='/give2-physical-business' className='no-underline blue '>
            {' '}
            njdete tu
          </a>
          )
        </p>

        <ul>
          <li>
            Poiadajte svojho zamestnvatea o vykonanie ronho ztovania
            zaplatench preddavkov na da (prloha . 1 iados o vykonanie
            ronho ztovania preddavkov).
          </li>
          <li>
            Po vykonan ztovania poiadajte zamestnvatea, aby Vm vystavil
            tlaivo Potvrdenie o zaplaten dane za minul rok (prloha . 2
            Potvrdenie o zaplaten dane).
          </li>
          <li>
            Vyplte Vyhlsenie o poukzan 2% z dane pre OZ Prd (prloha . 3
            Vyhlsenie o poukzan dane) poda pouenia (prloha . 3a Pouenie
            k Vyhlseniu o poukzan dane).
          </li>
          <li>
            {' '}
            Potvrdenia o zaplaten dane zistte potrebn daje na vyplnenie
            Vyhlsenia o poukzan dane, t. j. dtum zaplatenia dane a sumu
            zaplatenej dane na vpoet prslunch percent:
            <p>
              1. 2% zo svojej zaplatenej dane - ide o maximlnu sumu, ktor
              mete poukza v prpade, e ste v minulom roku neboli
              dobrovonkom, alebo ste dobrovoncky odpracovali menej ako 40
              hodn. Minimlna vka tejto sumy je vak 3 .{' '}
            </p>
            <p>
              2. 3% zo svojej zaplatenej dane  tka sa len osb, ktor v
              minulom roku dobrovoncky odpracovali minimlne 40 hodn.
              Potvrdenie o tom od organizcie, resp. organizci, v ktorch ste
              psobili, je povinnou prlohou k vyhlseniu a potvrdeniu o
              zaplaten dane.
            </p>
          </li>
          <li>
            Vyplnen Vyhlsenie o poukzan 2% a tlaivo Potvrdenie o zaplaten
            dane (dobrovonci s potom aspo 40 odpracovanch hodn za minul
            rok aj s potvrdenm od prslunej organizcie, resp. organizci)
            dorute do 30.04.2022 na Daov rad poda miesta svojho trvalho
            bydliska, nie poda sdla zamestnvatea.
          </li>
          <li>
            Po splnen podmienok maj daov rady 90 dn na prevedenie 2% alebo
            3% v prospech obianskeho zdruenia Prd.
          </li>
        </ul>
        <div className='my-5'>
          <strong>Potrebn tlaiv njdete pod lnkom</strong>
          <p>Prloha . 01 - iados o ron ztovanie</p>
          <p>Prloha . 02 - Potvrdenie o zaplaten dane</p>
          <p>Prloha . 03 - Vyhlsenie o poukzan dane</p>
          <p>Prloha . 03a - Pouenie k Vyhlseniu o poukzan dane</p>
        </div>
        <div className='my-5'>
          <strong>pravy pre dobrovonkov</strong>
          <p>
            V prpade, e fyzick osoba (zamestnanec alebo ivnostnk) pre
            akkovek neziskov organizciu alebo jednotlivca (prijmatea
            dobrovonckej innosti) poas minulho roka odpracovala ako
            dobrovonk aspo 40 hodn a tto organizcia/jednotlivec o tom vyd
            Potvrdenie o vkone dobrovonckej innosti, me fyzick osoba
            venova a 3% zo svojej dane! V tom prpade vo Vyhlsen vypotajte
            3% zo svojej dane a k Vyhlseniu prilote okrem Potvrdenia o
            zaplaten dane aj Potvrdenie o vkone dobrovonckej innosti.
            Dobrovonk nemusel odpracova 40 hodn vlune v prospech jedinej
            organizcie/ jednotlivca. Poet minimlne 40 hodn tak me
            vysklada aj z viacerch Potvrden od viacerch
            organizci/jednotlivcov.
          </p>
        </div>

        <div className='files-btns my-5'>
          <strong>Sbory na stiahnutie:</strong>

          <Button
            className='btn-file'
            onClick={() => downloadFileHandler(file1)}
          >
            iados o ron ztovanie
          </Button>
          <Button
            className='btn-file'
            onClick={() => downloadFileHandler(file2)}
          >
            Potvrdenie o zaplaten dane
          </Button>
          <Button
            className='btn-file'
            onClick={() => downloadFileHandler(file3)}
          >
            Vyhlsenie o poukzan dane
          </Button>
          <Button
            className='btn-file'
            onClick={() => downloadFileHandler(file4)}
          >
            Pouenie k vyhlseniu o poukzan dane
          </Button>
        </div>
      </div>
    </>
  )
}

export default Give2Physical
