import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Form, Button } from 'react-bootstrap'
import { useDispatch } from 'react-redux'
import Message from '../components/Message'
import FormContainer from '../components/FormContainer'
import { createDiscount } from '../actions/productActions'

const CreateDiscount = () => {
  const [discount, setDiscount] = useState('')
  const [messageSuccess, setMessageSuccess] = useState(null)

  const dispatch = useDispatch()

  const submitHandler = () => {
    dispatch(
      createDiscount({
        discount: discount,
      })
    )
    if (discount > 0) {
      setMessageSuccess(`Akcia vytvoren`)
    } else {
      setMessageSuccess(`Akcia zruen`)
    }
  }

  console.log(discount)
  return (
    <>
      <Link to='/admin/productlist' className='btn btn-back my-3'>
        Nasp
      </Link>
      <FormContainer>
        <h1>Nov akcia na vetky produkty</h1>
        {messageSuccess && (
          <Message variant='success'>{messageSuccess}</Message>
        )}

        <Form onSubmit={submitHandler}>
          <Form.Group controlId='discount-value'>
            <Form.Label>
              Vka akcie bez %. Akciu mono zrui zadanm 0.
            </Form.Label>
            <Form.Control
              type='text'
              placeholder='zava'
              value={discount}
              onChange={(e) => setDiscount(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Button className='my-5 btn-blue' type='submit' variant='primary'>
            Vytvori
          </Button>
        </Form>
      </FormContainer>
    </>
  )
}

export default CreateDiscount
